<template>
    <v-container :class="{'h-full': booksItem === undefined }">
        <v-row v-if="booksItem !== undefined">
            <v-col cols="12">
                <v-card>
                    <v-container>
                        <v-row dense>
                            <v-col 
                                cols="5"
                                sm="2"
                                lg="1"
                                class="d-sm-flex align-sm-center"
                            >
                                <v-card 
                                    class="pa-3 h-full"
                                >
                                    <v-img
                                        :src="booksItem.cover"
                                        contain
                                        :height="$vuetify.breakpoint.xsOnly || $vuetify.breakpoint.lgOnly ? '100%': ''"
                                    >
                                    </v-img>
                                </v-card>
                            </v-col>
                            <v-col 
                                cols="7"
                                sm="10"
                                lg="11"
                                class="d-md-flex flex-md-column"
                            >
                                <v-card class="flex-md-grow-1">
                                    <v-card-title>{{ booksItem.title }}</v-card-title>
                                    <v-card-subtitle>{{booksItem.author}}</v-card-subtitle>
                                    <v-divider class="d-sm-none" />
                                    <v-btn
                                        icon
                                        @click="isOpenMainDesctiption = !isOpenMainDesctiption"
                                        class="d-flex d-sm-none ml-auto"
                                    >
                                        <v-icon>mdi-chevron-{{ isOpenMainDesctiption ? "up" : "down" }}</v-icon>
                                    </v-btn>

                                    <v-expand-transition>
                                        <div v-show="isOpenMainDesctiption">
                                            <v-divider />
                                            <v-card-actions class="pa-sm-0">
                                                <v-container 
                                                    fluid
                                                    class="pa-0"
                                                >
                                                    <v-row 
                                                        dense 
                                                        no-gutters
                                                        class="px-sm-2"
                                                    >
                                                        <v-col 
                                                            cols="12"
                                                            sm="6"
                                                            class="align-self-center"
                                                        >
                                                            <div class="flex-sm-grow-1">
                                                                <v-tooltip 
                                                                    bottom
                                                                    v-for="genre in booksItem.genre"
                                                                    :key="genre"
                                                                >
                                                                    <template v-slot:activator="{ on, attrs }">
                                                                        <v-icon
                                                                            v-bind="attrs"
                                                                            v-on="on"
                                                                        >
                                                                            mdi-{{genre.icon}}
                                                                        </v-icon>
                                                                    </template>
                                                                    <span>{{genre.text}}</span>
                                                                </v-tooltip>
                                                            </div>
                                                        </v-col>
                                                        <v-col
                                                            cols="12" 
                                                            sm="6"
                                                            class="d-sm-flex justify-sm-end"
                                                        >
                                                            <v-chip-group>
                                                                <v-chip 
                                                                    v-for="copy in checkCurentCopy"
                                                                    :key="copy"
                                                                    color="info"
                                                                >
                                                                    {{copy}}
                                                                </v-chip>
                                                            </v-chip-group>
                                                        </v-col>
                                                    </v-row>
                                                    <v-row 
                                                        dense 
                                                        no-gutters
                                                    >
                                                        <v-col 
                                                            v-show="$vuetify.breakpoint.smAndUp"
                                                            cols="12"
                                                        >
                                                            <v-divider />
                                                            <div class="d-sm-flex">
                                                                <v-rating
                                                                    half-increments
                                                                    dense
                                                                    :value="booksItem.rating"
                                                                    class="flex-sm-grow-1 align-self-center pl-sm-2"
                                                                />
                                                                <v-btn color="success">
                                                                    {{booksItem.price}}
                                                                    <v-icon 
                                                                        right
                                                                    >
                                                                        mdi-currency-rub
                                                                    </v-icon>
                                                                </v-btn>
                                                            </div>
                                                        </v-col>
                                                    </v-row>
                                                </v-container>
                                            </v-card-actions>
                                        </div>
                                    </v-expand-transition>
                                </v-card>
                            </v-col>
                            <v-col 
                                cols="12"
                            >
                                <v-card>
                                    <v-card-subtitle>Отзывы</v-card-subtitle>
                                    <v-divider />
                                    <v-list three-line>
                                        <template
                                            v-for="(item, i) in testReviews"
                                        >
                                            <v-divider :key="i" v-if="item.divider" />
                                            <v-list-item 
                                                :key="item.name"
                                                v-else
                                            >
                                                <v-list-item-avatar>
                                                    <v-icon>mdi-{{ item.avatar }}</v-icon>
                                                </v-list-item-avatar>
                                                <v-list-item-content>
                                                    <v-list-item-title>{{ item.name }}</v-list-item-title>
                                                    <v-list-item-subtitle>{{ item.text }}</v-list-item-subtitle>
                                                </v-list-item-content>
                                            </v-list-item>
                                        </template>
                                    </v-list>
                                </v-card>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col 
                                cols="12"
                            >
                                <v-btn
                                    block
                                    @click="isOpenDescriptionCard = !isOpenDescriptionCard"
                                >
                                    <p class="mb-0 flex-grow-1">{{isOpenDescriptionCard ? "Закрыть" : "Открыть"}} описание</p>
                                    <v-icon>mdi-chevron-{{ isOpenDescriptionCard ? "up" : "down" }}</v-icon>
                                </v-btn>
                                <v-expand-transition>
                                    <v-card v-show="isOpenDescriptionCard">
                                        <v-card-title>Описание</v-card-title>
                                        <v-divider></v-divider>
                                        <v-card-actions>
                                            <v-card-text>{{booksItem.description}}</v-card-text>
                                        </v-card-actions>
                                    </v-card>
                                </v-expand-transition>
                            </v-col>
                        </v-row>
                        <v-row v-show="$vuetify.breakpoint.xsOnly">
                            <v-divider></v-divider>
                            <v-col cols="12">
                                <v-card>
                                    <v-card-actions>
                                        <v-rating
                                            half-increments
                                            dense
                                            :value="booksItem.rating"
                                            class="flex-grow-1"
                                        />
                                        <v-btn color="success">
                                            {{booksItem.price}}
                                            <v-icon 
                                                right
                                            >
                                                mdi-currency-rub
                                            </v-icon>
                                        </v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>
        <v-row 
            v-else
            class="h-full"
            align="center"
        >
            <v-col cols="12">
                <v-card>
                    <div class="d-flex">
                        <v-card-subtitle>
                            Не найдено
                            <v-icon>mdi-help</v-icon>
                        </v-card-subtitle>
                        <v-card-actions class="flex-grow-1 justify-end">
                            <v-btn 
                                @click="backRoute"

                            >
                                Вернуться назад
                                <v-icon>mdi-restart</v-icon>
                            </v-btn>
                        </v-card-actions>
                    </div>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
    export default {
        name: "Books",
        data: () => ({
            isOpenDescriptionCard: false,
            isOpenMainDesctiption: false,
            testReviews: [
            { 
                name: "test", 
                text: 
                `
                    Предварительные выводы неутешительны: 
                    постоянный количественный рост и сфера 
                    нашей активности не даёт нам иного выбора, 
                    кроме определения первоочередных требований.
                `,
                avatar: "account"
            }, 
            { divider: true, },
            {
                name: "test2", 
                text: 
                `
                    Также как дальнейшее развитие различных форм деятельности 
                    требует анализа приоретизации разума над эмоциями.
                `,
                avatar: "user"
            }
        ],
        }),
        methods: {
            backRoute() {
                return history.back();
            },
        },
        computed: {
            booksItem() {
                return this.$store.state.books.books.item[this.$route.params.id];
            },
            checkCurentCopy() {
                const onlineCopy = this.$store.state.books.books.item[this.$route.params.id].copy.online;
                const physicalCopy = this.$store.state.books.books.item[this.$route.params.id].copy.physical;


                if(onlineCopy && !physicalCopy) {
                    return ["Только онлаин копия"];
                }
                else if (physicalCopy && !onlineCopy) {
                    return ["Только физическая копия"]
                }
                else {
                    return ["Физическая копия", "Онлаин копия"]
                }
            }, 
            removeExpandOnSm() {
                let smAndUp = this.$vuetify.breakpoint.smAndUp; 
                if(smAndUp) {
                    return this.isOpenMainDesctiption = true;
                }
                return smAndUp;
            }
        },
    }
</script>